@charset "utf-8";

.project {
	padding: 40px 50px 40px 0;
}

/* 프로젝트 타이틀 */
.title {
	margin-bottom: 30px;
	text-align: center;
	font-size: 40px;
}
.title span {
	font-family: 'Cafe24Ssurround';
	text-shadow: -2px 0px #000000, 0px 2px #000000, 2px 0px #000000, 0px -2px #000000;
}
.title span:first-child {
	color: #ffffff;
}
.title .red {
	color: #ff5656;
}
.title .blue {
	color: #84adec;
}
.title .pupple {
	color: #b47edf;
}
.title .yellow {
	color: #ffdb5a;
}
.title .green {
	color: #63c3a1;
}

/* 프로젝트 콘텐츠 영역 */
.contBox {
	display: flex;
	align-items: center;
}

/* 프로젝트 이미지 박스 영역 */
.imgBox {
	width: 55%;
	margin-top: 30px;
	margin-right: 40px;
	text-align: center;
}
.imgBox img {
	max-width: 98%;
	max-height: 95%;
	padding-bottom: 40px;
}

/* 프로젝트 텍스트 영역 */
.txtBox {
	width: 45%;
	padding-left: 30px;
}
/* dl 정의 영역 */
.detail {
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	margin-bottom: 30px;
}
.detail dt,
.detail dd {
	margin: 10px 0;
}
.detail dt {
	width: 120px;
	font-size: 20px;
	font-family: 'Cafe24Ssurround';
	align-self: self-start;
}
.detail dd {
	width: calc(100% - 120px);
	word-break: keep-all;
	line-height: 24px;
}

/* 나의 제작 페이지 */
.myMaking {
	padding: 1px 3px;
	background-color: #ffdb5a;
}

/* 바로가기 링크 */
.link {
	width: 240px;
	margin-right: 10px;
	border: none;
	border-radius: 10px;
	font-size: 16px;
	font-weight: bold;
	box-shadow: 0 2px 4px #858585;
	cursor: pointer;
}
.link a {
	display: block;
	padding: 15px 10px;
	border-radius: 10px;
	color: #000000;
	background-color: #ffdb5a;
}
.link a:hover {
	color: #ffffff;
	background-color: #33c06e;
}

.loginBox {
	margin-top: 5px;
}
