@charset "utf-8";

/* 메인 서식 */
main {
	position: relative;
	width: 100%;
	height: 100vh;
	background-color: #191919;
	overflow: hidden;
}

/* 내비게이션 */
.nav {
	position: absolute;
	bottom: -10px;
	left: 50%;
	transform: translateX(-50%);
	width: 1200px;
	height: 210px;
	clip-path: polygon(10% 0, 90% 0, 100% 100%, 0% 100%);
	background-color: #aaaaaa;
	border: 10px solid #000000;
}
.nav::before {
	content: '';
	position: absolute;
	width: 280px;
	height: 10px;
	top: 90px;
	left: -85px;
	transform: rotate(300deg);
	background-color: #000000;
}
.nav::after {
	content: '';
	position: absolute;
	width: 280px;
	height: 10px;
	top: 90px;
	right: -85px;
	transform: rotate(60deg);
	background-color: #000000;
}

/* 로딩창 nav */
.navLoading {
	display: flex;
	justify-content: space-evenly;
	align-items: center;
	padding: 0 60px;
}
.navLoading div {
	width: 48px;
	height: 48px;
	border-radius: 50%;
	background-color: #000000;
}
.navLoading p {
	font-family: 'Cafe24Ssurround';
	font-size: 40px;
	color: #ffffff;
	text-shadow: -2px 0px #000000, 0px 2px #000000, 2px 0px #000000, 0px -2px #000000;
}

/* 일반 nav */
.navNormal {
	text-align: center;
	bottom: -160px;
	transition: 1s;
}

/* 일반 nav 버튼 서식 */
.navBtnList {
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 0 auto;
}
.navBtn {
	cursor: pointer;
	margin: 20px 18px;
}
.navBtnSquare {
	height: 140px;
	padding: 20px 25px;
	border: 3px solid transparent;
	border-radius: 10px;
	font-size: 38px;
	font-family: 'Cafe24Ssurround';
	color: #ffffff;
	background-color: #222222;
	box-shadow: inset 0 5px 0px rgba(58, 58, 56, 0.8), inset 0 -5px 0 rgba(0, 0, 0, 0.8), 0 0 8px rgba(7, 4, 34, 0.5);
}
.navBtnSquare:hover {
	border: 3px solid #ffffff;
}
.navBtnSquare p {
	font-family: 'Cafe24Ssurround';
}
.navBtnColor1 {
	color: #ff5656;
}
.navBtnColor2 {
	color: #ffdb5a;
}

.navBtnRound {
	width: 110px;
	height: 110px;
	border: none;
	border-radius: 50%;
	background-color: #ff2121;
	box-shadow: inset 0 0 0px 5px rgba(62, 62, 62, 0.8), 0 0 8px rgba(7, 4, 34, 0.5);
}

/* nav 버튼 누를시 나오는 리스트 서식*/
.navMenuList {
	display: none;
	position: absolute;
	bottom: 180px;
	padding: 10px 17px;
	border: 1px solid #000000;
	border-radius: 10px;
	font-size: 36px;
	background-color: rgba(255, 255, 255, 0.7);
	text-shadow: -1px 0px #000000, 0px 1px #000000, 1px 0px #000000, 0px -1px #000000;
}
.navMenuList li {
	font-family: 'Cafe24Ssurround';
	padding: 5px 0;
	border-bottom: 1px solid #000000;
	line-height: 54px;
	text-align: center;
	cursor: pointer;
}
.navMenuList li:last-of-type {
	border-bottom: none;
}
.navSmallTxt {
	font-size: 32px;
}
/* 팀 프로젝트 리스트 위치 */
.navMenuListTeam {
	display: block;
	left: 50%;
	transform: translateX(-193%);
}
/* 나의 프로젝트 리스트 위치 */
.navMenuListMy {
	display: block;
	left: 50%;
	transform: translateX(-78%);
}

/* 팀 프로젝트 리스트 색상 */
.navMenuListTeam li:first-of-type {
	color: #ff5656;
}
.navMenuListTeam li:last-of-type {
	color: #84adec;
}
.navMenuListMy li:first-of-type {
	color: #b47edf;
}
.navMenuListMy li:nth-of-type(2) {
	color: #ffdb5a;
}
.navMenuListMy li:last-of-type {
	color: #63c3a1;
}

/* 내비게이션 위치 알림 */
.navInform {
	position: absolute;
	bottom: 50px;
	left: 50%;
	height: 30px;
	transform: translateX(-50%);
	color: #ffffff;
}
.navInformIcon {
	font-size: 24px;
	margin-right: 3px;
}
.navInformIcon svg {
	vertical-align: middle;
}
.navInformTxt {
	font-size: 16px;
}

/* 섹션 공통 */
section {
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
	top: 90px;
	width: calc(100% - 180px);
	height: 65vh;
	border-radius: 20px;
	background-color: #ffffff;
	overflow: hidden;
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
	transition: 1s;
}

.sectionInner2 {
	position: absolute;
	display: flex;
	top: 0;
	left: 99%;
	width: 100%;
	height: 100%;
	border-radius: 30px;
	background-color: #ffcd42;
	transform: scaleY(0.8);
	transition: 1s;
}
.sectionInnerClick {
	width: 1%;
	height: 100%;
	border-radius: 30px 0 0 30px;
	background-color: #63c3a1;
	cursor: pointer;
	z-index: 1;
}
.sectionInnerScale {
	animation: sectionInnerMove 0.7s ease-in-out forwards;
}

@keyframes sectionInnerMove {
	0% {
		transform: scaleY(0.8);
	}
	100% {
		transform: scaleY(1);
		left: 0;
		border-radius: 0;
	}
}

/* 메인 상단 신호등 모양 */
.signal {
	position: absolute;
	top: 50px;
	left: 50%;
	transform: translate(-50%, -50%);
	display: flex;
	justify-content: center;
	width: 300px;
	margin: 0 auto;
}
.signal > div {
	width: 22px;
	height: 22px;
	border-radius: 50%;
	margin: 0 8px;
}
.signalRed {
	background-color: #ff5656;
	animation: signal 3s ease-out infinite;
}
.signalYellow {
	background-color: #ffad32;
	animation: signal 3s ease-out 0.3s infinite;
}
.signalBlue {
	background-color: #84adec;
	animation: signal 3s ease-out 0.6s infinite;
}
.signalGreen {
	background-color: #63c3a1;
	animation: signal 3s ease-out 0.9s infinite;
}

@keyframes signal {
	0% {
		opacity: 1;
	}
	50% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

/* 메인 각 모서리 동글동글 나사 애니메이션 */
@keyframes around {
	0% {
		transform: rotate(0);
	}
	50% {
		color: #ffffff;
		/* transform: rotate(180deg); */
	}
	100% {
		transform: rotate(360deg);
	}
}

/* 메인 각 모서리 동글동글 나사 */
.screw {
	position: absolute;
	width: 70px;
	height: 70px;
	border: 2px solid #ffffff;
	border-radius: 50%;
	text-align: center;
	background-color: #bdbdbd;
	box-shadow: 0 0 8px rgba(0, 0, 0, 0.4);
}
.screw i {
	font-size: 52px;
	line-height: 70px;
	/* color: #ffffff; */
}
.screw i svg {
	vertical-align: middle;
	color: #191919;
}
.screw1 {
	top: 15px;
	left: 15px;
}
.screw1 i svg {
	/* color: rgb(213, 177, 58); */
	animation: around 5s ease-in-out infinite;
}
.screw2 {
	top: 15px;
	right: 15px;
}
.screw2 i svg {
	/* color: rgb(45, 140, 178); */
	animation: around 4s ease-in-out infinite;
}
.screw3 {
	bottom: 15px;
	left: 15px;
}
.screw3 i svg {
	/* color: rgb(202, 77, 112); */
	animation: around 3s ease-in-out infinite;
}
.screw4 {
	bottom: 15px;
	right: 15px;
}
.screw4 i svg {
	/* color: rgb(13, 140, 55); */
	animation: around 5s ease-in-out infinite;
}

/* 섹션 가장자리 네모 데코 */
.boxDeco {
	position: absolute;
	background-color: #191919;
}
.boxDeco1 {
	width: 120px;
	height: 20px;
	top: 80px;
	right: 180px;
}
.boxDeco2 {
	width: 20px;
	height: 120px;
	top: 480px;
	left: 80px;
}

/* 섹션 아래족 고양이 데코 */
.cat {
	position: absolute;
	width: 180px;
	right: 130px;
	bottom: 15vh;
	transition: 1s;
}
