@charset "utf-8";

/* 로딩창 배경 이미지 */
.imgBox {
	width: 100%;
	height: 100%;
	background: url('../assets/common/loading1.jpg');
	background-position: 50%;
	background-repeat: no-repeat;
	background-size: cover;
	opacity: 50%;
}

/* 아래로 흐르는 선 */
.lineBox {
	animation: lineMove 6s linear infinite;
}
.line {
	width: 100%;
	height: 80px;
	background-color: #4a4a4a;
	opacity: 30%;
	margin-bottom: 80px;
}

@keyframes lineMove {
	0% {
		transform: translateY(-80px);
	}
	100% {
		transform: translateY(80px);
	}
}

/* 로딩창 텍스트 문구 */
.txtBox {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%);
	font-size: 32px;
}
