@charset "utf-8";

/* nav호버에 따라 섹션 움직이기 */
.sectionDownAni {
	animation: sectionDownMove 0.3s linear;
	height: 80vh;
}

@keyframes sectionDownMove {
	0% {
		height: 65vh;
	}
	100% {
		height: 80vh;
	}
}

.sectionUpAni {
	animation: sectionUpMove 0.3s linear;
	/* height: 65vh; */
}
@keyframes sectionUpMove {
	0% {
		bottom: 80vh;
	}
	100% {
		bottom: 65vh;
	}
}

/* nav호버에 따라 고양이 데코 움직이기 */
.catDownAni {
	animation: catDownMove 0.3s linear;
	bottom: 0vh;
}

@keyframes catDownMove {
	0% {
		bottom: 15vh;
	}
	100% {
		bottom: 0vh;
	}
}

.catUpAni {
	animation: catUpMove 0.6s linear;
	bottom: 15vh;
}
@keyframes catUpMove {
	0% {
		bottom: 0vh;
	}
	100% {
		bottom: 15vh;
	}
}
