@charset "utf-8";
/* 공통 */
.hidden {
	display: none;
}
/*----------------- 홈 첫번째 화면 ------------------*/
.home {
	display: flex;
}
/* 왼쪽 이미지 영역 */
.leftBox {
	position: relative;
	padding: 100px 20px 100px 80px;
}

.leftBox .imgRound {
	position: absolute;
	top: 90px;
	left: 70px;
	width: 400px;
	height: 400px;
	border: 3px solid #ffffff;
	border-radius: 50%;
	cursor: pointer;
	animation: imgRound 2s linear infinite;
}
.profileImg {
	width: 400px;
	margin-bottom: 40px;
	cursor: pointer;
}
.profileBlack {
	position: absolute;
	top: 100px;
	width: 400px;
	height: 400px;
	border-radius: 50%;
	background-color: #000000;
}

/* 다음 버튼 */
.nextBoxColor {
	width: 240px;
	height: 100px;
	margin: 0 auto;
	border-radius: 12px;
	cursor: pointer;
	text-align: center;
	line-height: 84px;
	background-color: #ffaeae;
	animation: colorChange 8s linear infinite;
}
.nextBox {
	width: 220px;
	height: 80px;
	margin: 0 auto;
	border-radius: 7px;
	background-color: rgba(255, 255, 255, 0.9);
	transform: translateY(10px);
	transition: 0.3s;
}
.nextBox:hover {
	color: #ffffff;
	background-color: #292929;
}
.nextTxt {
	font-weight: bold;
	font-size: 24px;
}
.nextImg {
	width: 30px;
	vertical-align: middle;
	transform: translateY(-3px);
}

/* 다음 버튼 클릭시 나오는 걷는 사람 */
.charWalk {
	position: absolute;
	width: 100px;
	animation-fill-mode: forwards;
	animation-name: charDown;
	animation-duration: 3.4s;
	animation-delay: 0;
	animation-timing-function: ease-in-out;
	transition: 1s;
}
.charWalkImg {
	width: 100px;
}

@keyframes charDown {
	0% {
		top: 270px;
		left: 400px;
	}
	10% {
		top: 420px;
		left: 400px;
	}
	100% {
		top: 420px;
		left: 95%;
	}
}

/* 프로필 이미지 테두리 라운드 */
@keyframes imgRound {
	0% {
		transform: rotate(0);
	}
	50% {
		width: 415px;
	}
	100% {
		transform: rotate(360deg);
	}
}

/* 다음 버튼 테두리 색상 변경 */
@keyframes colorChange {
	0% {
		background-color: #ffaeae;
	}
	20% {
		background-color: #ffd88a;
	}
	50% {
		background-color: #50ef98;
	}
	75% {
		background-color: #77b8f5;
	}
	100% {
		background-color: #ffaeae;
	}
}

/* 오른족 영역 */
/* 오른쪽 - 인삿말 영역*/
.rightBox {
	width: 100%;
	padding: 80px 0 0;
}
.helloBox {
	text-align: center;
	font-size: 28px;
	line-height: 1.4;
	margin-bottom: 80px;
}
.helloBox span {
	border-bottom: 5px solid #ff4226;
}

/* 프로필 & 스킬 */
.contBox {
	display: flex;
	justify-content: center;
	font-size: 22px;
}
.contBox > div {
	position: relative;
	padding: 50px 30px;
	border: 1px solid #000000;
}
.contBox div h3 {
	position: absolute;
	padding: 11px 48px;
	border: 1px solid #000000;
	top: -24px;
	left: 15px;
	background-color: #fffdf4;
}

/* 프로필 */
.profileBox {
	width: 45%;
	margin-right: 20px;
}
.profileBox dl {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	width: 100%;
	line-height: 50px;
}
.profileBox dt {
	width: 30%;
	/* font-weight: bold; */
}
.profileBox dd {
	position: relative;
	width: 70%;
	font-weight: bold;

	/* font-weight: bold; */
}
.profileBox dd::before {
	position: absolute;
	top: 17px;
	left: -10px;
	content: '';
	clip-path: polygon(10% 0%, 100% 0%, 90% 100%, 0% 100%);
	width: 84px;
	height: 14px;
	background-color: #ffe8ac;
	z-index: -1;
}
.profileBox dd:nth-of-type(2):before {
	clip-path: polygon(6% 0%, 100% 0%, 94% 100%, 0% 100%);
	width: 146px;
}
.profileBox dd:nth-of-type(3):before {
	clip-path: polygon(4% 0%, 100% 0%, 96% 100%, 0% 100%);
	width: 210px;
}
.profileBox dd:nth-of-type(4):before {
	clip-path: polygon(4% 0%, 100% 0%, 96% 100%, 0% 100%);
	width: 230px;
}
.profileBox dd:nth-of-type(5):before {
	clip-path: polygon(4% 0%, 100% 0%, 96% 100%, 0% 100%);
	width: 210px;
}
.profileBox .intro {
	margin: 20px 0 0;
	color: #0e6864;
	font-weight: bold;
}

/* 스킬 */
.skillBox {
	width: 45%;
}
.skillBox ul {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
}
.skillBox li {
	margin: 5px;
	padding: 10px;
	border-radius: 10px;
	background-color: #ffebb7;
	cursor: pointer;
	transition: 0.5s;
}
.skillBox li:hover {
	background-color: #84adec;
}
.skillBox ul li img {
	height: 60px;
	vertical-align: middle;
}

/* 스킬 클릭 유도 텍스트 */
.skillClick {
	position: absolute;
	font-weight: 700;
	font-size: 26px;
}
.skillClick1 {
	top: 28px;
	right: 30px;
	transform: rotate(30deg);
	animation: skillClick1 3s ease-in-out infinite;
}
.skillClick2 {
	top: 120px;
	left: -15px;
	transform: rotate(346deg);
	animation: skillClick2 3.3s ease-in-out infinite;
}
.skillClick::before {
	content: '';
	position: absolute;
	top: 8px;
	left: -5px;
	width: 70px;
	height: 14px;
	background-color: #ffaeae;
	z-index: -1;
}
.skillClick1::before {
	transform: rotate(14deg);
}

@keyframes skillClick1 {
	50% {
		transform: rotate(30deg);
	}
	60% {
		transform: rotate(15deg);
	}
	70% {
		transform: rotate(35deg);
	}
	80% {
		transform: rotate(30deg);
	}
	90% {
		transform: rotate(40deg);
	}
	100% {
		transform: rotate(30deg);
	}
}

@keyframes skillClick2 {
	50% {
		transform: rotate(346deg);
	}
	60% {
		transform: rotate(330deg);
	}
	70% {
		transform: rotate(350deg);
	}
	80% {
		transform: rotate(340deg);
	}
	90% {
		transform: rotate(350deg);
	}
	100% {
		transform: rotate(346deg);
	}
}

/* 스킬 모달 */
.modalBox {
	position: fixed;
	width: 100%;
	height: 100vh;
	background-color: rgba(0, 0, 0, 0.5);
	z-index: 1;
}
.modal {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 600px;
	height: 370px;
	padding: 25px;
	border-radius: 10px 10px 0 0;
	background-color: #2f4aac;
}
.modalTop {
	display: flex;
	justify-content: space-between;
	align-items: center;
	font-size: 24px;
	height: 40px;
	color: #ffffff;
}
.modalTop h3 {
	padding-bottom: 6px;
}
.modalTop p {
	font-size: 50px;
	cursor: pointer;
}
.modalTop p path {
	vertical-align: middle;
}
.modalCont {
	display: flex;
	justify-content: space-between;
	align-items: center;
	background-color: #fffdf4;
	height: 87%;
	padding: 10px 30px 10px 20px;
	text-align: center;
	font-weight: bold;
}
.modalImgBox {
	width: 162px;
	text-align: center;
	margin-right: 20px;
}
.modalCont img {
	height: 109px;
}
.modalTxt {
	width: calc(100% - 162px);
	text-align: center;
	font-size: 18px;
	line-height: 1.4;
	white-space: pre-wrap;
	word-break: keep-all;
}

/*----------------- 홈 두번째 화면 ------------------*/
.sectionInner {
	position: absolute;
	overflow: hidden;
	width: 100%;
}

/* 흐르는 텍스트 */
/* 아래로 흐르는 선 */
.backTextBox {
	display: flex;
	position: absolute;
	top: 100px;
	width: 4800px;
	z-index: -1;
	animation: backText 20s linear infinite;
}
.backText {
	width: 1200px;
	font-size: 70px;
	color: #ffd66f;
	/* font-weight: bold; */
	font-family: 'Cafe24Ssurround';
}

@keyframes backText {
	0% {
		left: 0px;
	}
	100% {
		left: -1200px;
	}
}

/* 본격적인 내부 콘텐츠 */
.sectionInnerCont {
	padding: 50px;
}
/* 백스페이스 버튼 */
.backSpace {
	position: absolute;
	right: 50px;
	width: 200px;
	padding: 10px;
	border-radius: 6px;
	background-color: #aea9a1;
	text-align: center;
	cursor: pointer;
	box-shadow: 1px 4px 4px rgba(0, 0, 0, 0.4);
}
.backSpace p {
	padding: 12px 0;
	border-radius: 8px;
	line-height: 24px;
	font-size: 24px;
	background-color: #f4e9d7;
	font-family: 'Cafe24Ssurround';
}
.backSpace span {
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
	bottom: 10px;
	font-size: 12px;
}
/* 타이틀 */
.title {
	font-size: 80px;
	font-family: 'Cafe24Ssurround';
	margin-bottom: 20px;
}
.adjective {
	width: 330px;
	height: 91px;
	border: none;
	border-bottom: 8px solid #268ff1;
	outline: none;
	font-size: 80px;
	font-weight: bold;
	text-align: center;
	color: #268ff1;
	background-color: transparent;
}
.introduce {
	font-size: 20px;
	font-weight: bold;
	margin-bottom: 20px;
}
