@charset "utf-8";

.navDownAni {
	animation-fill-mode: forwards;
	animation-name: navDownMove, navMove;
	animation-delay: 0s, 3s;
	animation-duration: 0.3s, 3s;
	animation-timing-function: linear, ease-in-out;
}

@keyframes navDownMove {
	0% {
		bottom: -10px;
	}
	100% {
		bottom: -160px;
	}
}

@keyframes navMove {
	0% {
		transform: translate(-50%, 0px);
	}
	20% {
		transform: translate(-50%, 10px);
	}
	40% {
		transform: translate(-50%, 0px);
	}
	60% {
		transform: translate(-50%, 10px);
	}
	80% {
		transform: translate(-50%, 0px);
	}
	100% {
		transform: translate(-50%, 10px);
	}
}

.navUpAni {
	animation: navUpMove 0.3s linear forwards;
}

@keyframes navUpMove {
	0% {
		bottom: -160px;
	}
	100% {
		bottom: -10px;
	}
}
