@charset "utf-8";

/* 폰트 */
@font-face {
	font-family: 'Cafe24SsurroundAir';
	src: url('https://gcore.jsdelivr.net/gh/projectnoonnu/noonfonts_2105_2@1.0/Cafe24SsurroundAir.woff') format('woff');
	font-weight: normal;
	font-style: normal;
}
@font-face {
	font-family: 'Cafe24Ssurround';
	src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_2105_2@1.0/Cafe24Ssurround.woff') format('woff');
	font-weight: normal;
	font-style: normal;
}

/* reset */
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: 'Cafe24SsurroundAir', '맑은 고딕', Arial, Helvetica, sans-serif;
	text-decoration: none;
}
ul,
ol,
li {
	list-style: none;
}

/* 공통 */
.sr-only {
	position: absolute;
	width: 1px;
	height: 1px;
	padding: 0;
	margin: -1px;
	overflow: hidden;
	clip-path: inset(50%);
	border: 0;
	clip: rect(0 0 0 0);
}

.hidden {
	display: none;
}
