@charset "utf-8";

.header {
	width: 100%;
	height: 240px;
	padding: 30px;
}
.headerYellow {
	background-color: #ffad32;
}
.headerBorder {
	display: flex;
	justify-content: space-between;
	width: 100%;
	height: 100%;
	padding: 20px 30px;
	border: 8px dashed #ffffff;
	border-radius: 10px;
}

/* 헤더 좌측 텍스트 */
.headerLeft * {
	color: #ffffff;
	font-family: 'Cafe24Ssurround';
}
.headerLeft h2 {
	margin: 10px 0 5px;
	text-shadow: -1px 0px #000000, 0px 1px #000000, 1px 0px #000000, 0px -1px #000000;
}
.headerTitle {
	display: flex;
	height: 70px;
}
.headerTitle h1 {
	margin-right: 10px;
	font-size: 70px;
	text-shadow: -1px 0px #000000, 0px 1px #000000, 1px 0px #000000, 0px -1px #000000;
}
.headerTitle p {
	font-size: 24px;
	align-self: self-end;
}

/* 헤더 우측 버튼들 */
.headerRight {
	display: flex;
}
.headerBtnBox {
	width: 100px;
	margin-left: 10px;
	text-align: center;
	/* color: #ffffff; */
	font-weight: bold;
}
.headerBtnBox button {
	width: 90px;
	height: 90px;
	border: none;
	border-radius: 50%;
	margin-bottom: 5px;
	background-color: #282828;
	cursor: pointer;
}
.headerBtnBox a {
	display: block;
	width: 100%;
	height: 100%;
	font-size: 60px;
	color: #ffffff;
}
.headerBtnBox a svg {
	vertical-align: middle;
	height: 100%;
}

.main {
	padding: 20px;
	background-image: url('../../public/assets/common/project/projectBackground.png');
}

.contTop {
	display: flex;
	border: 3px solid #000000;
	background-color: #ffad32;
}
.contCircleBox {
	width: 54px;
	border-right: 3px solid #000000;
}
.contCircle {
	width: 34px;
	height: 34px;
	margin: 6px auto;
	border-radius: 50%;
	background-color: #ffffff;
}
.contTop h3 {
	font-size: 24px;
	padding: 10px;
}
